.backdrop {
  z-index: 2;
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  background-color: rgba(5, 5, 5, .282);
  transition: opacity .25s linear, visibility .25s linear;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity .25s linear, visibility .25s linear;
}



